* {
  box-sizing: border-box;
  margin: 0;
  font-family: Roboto, serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header .logo-container {
  width: 40px;
  height: 40px;
}

.header .logo-container .logo {
  width: 100%;
  height: auto;
  display: block;
}

.header .nav {
  align-items: center;
  transition: color .3s;
  display: flex;
}

.header .nav .nav__item {
  height: 60px;
  margin-right: 20px;
  padding: 0;
  line-height: 60px;
}

.header .nav .nav__item:last-child {
  margin-right: 0;
}

.header .nav .nav__item .nav__link {
  color: #060b35;
  height: 60px;
  text-decoration: none;
  display: block;
}

.header .nav .nav__item .nav__link:hover {
  color: #00acdc;
}

.header .nav .nav__item .nav__link.is-active {
  color: #00acdc;
  height: 60px;
}

.header .nav .nav__item .nav__link.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  height: 4px;
  display: block;
}

.header .nav .nav__list {
  text-align: center;
  text-transform: uppercase;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  font-size: 12px;
  font-weight: 500;
  list-style: none;
  display: flex;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.main {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  margin: 0;
  padding: 50px 40px;
  font-family: Roboto, sans-serif;
  display: grid;
}

@media (width >= 488px) {
  .main {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .main {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .main {
    grid-template-columns: repeat(4, 200px);
  }
}

.card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
  transition: transform .3s, color .3s;
}

.card:hover {
  transform: scale(1.2);
}

.card__image {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__title {
  color: #060b35;
  margin-bottom: 4px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__title:hover {
  color: #34568b;
}

.card__code_text {
